<template>
  <div class="card">
    <div class="card-header">
      <h3 class="card-title">
        <i :class="icon"></i> {{ title }}
      </h3>
      <button type="button" class="btn btn-primary" @click="$emit('add')">
        <i class="fas fa-plus"></i> {{ addLabel }}
      </button>
    </div>
    <div class="card-body">
      <table class="grid-table">
        <thead>
          <tr>
            <th v-for="col in columns" :key="col.key">{{ col.label }}</th>
            <th v-if="hasActions" class="action-buttons">Aes</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="item in paginatedItems" :key="item.id">
            <td v-for="col in columns" :key="col.key">
              <template v-if="col.type === 'status'">
                <span :class="['status-badge', item[col.key] ? 'active' : 'inactive']">
                  {{ item[col.key] ? 'Ativo' : 'Inativo' }}
                </span>
              </template>
              <template v-else>
                {{ item[col.key] }}
              </template>
            </td>
            <td v-if="hasActions" class="action-buttons">
              <button class="edit-btn" title="Editar" @click="$emit('edit', item)"><i class="fas fa-edit"></i></button>
              <button class="delete-btn" title="Excluir" @click="$emit('delete', item)"><i class="fas fa-trash"></i></button>
            </td>
          </tr>
          <tr v-if="!paginatedItems.length">
            <td :colspan="columns.length + (hasActions ? 1 : 0)" class="empty-message">Nenhum registro encontrado</td>
          </tr>
        </tbody>
      </table>
      <div class="grid-pagination">
        <div class="pagination-info">
          Exibindo {{ start + 1 }}-{{ end }} de {{ items.length }} registros
        </div>
        <div class="pagination-controls">
          <button :disabled="page === 1" @click="setPage(page - 1)"><i class="fas fa-chevron-left"></i></button>
          <span>Pgina {{ page }} de {{ totalPages }}</span>
          <button :disabled="page === totalPages" @click="setPage(page + 1)"><i class="fas fa-chevron-right"></i></button>
          <select v-model="pageSize" @change="setPageSize">
            <option v-for="size in sizes" :key="size" :value="size">{{ size }}</option>
          </select>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, computed } from 'vue'
const props = defineProps({
  title: { type: String, default: '' },
  icon: { type: String, default: 'fas fa-layer-group' },
  addLabel: { type: String, default: 'Adicionar' },
  columns: { type: Array, required: true },
  items: { type: Array, required: true },
  hasActions: { type: Boolean, default: true },
  sizes: { type: Array, default: () => [10, 25, 50, 100] }
})
const page = ref(1)
const pageSize = ref(10)
const totalPages = computed(() => Math.max(1, Math.ceil(props.items.length / pageSize.value)))
const start = computed(() => (page.value - 1) * pageSize.value)
const end = computed(() => Math.min(page.value * pageSize.value, props.items.length))
const paginatedItems = computed(() => props.items.slice(start.value, end.value))
function setPage(val) { page.value = val }
function setPageSize() { page.value = 1 }
</script>

<style scoped>
.card {
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.04);
  padding: 0;
  margin-bottom: 2rem;
}
.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.25rem 1.5rem 0.5rem 1.5rem;
}
.card-title {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-size: 1.2rem;
  font-weight: 600;
  color: #222;
}
.btn-primary {
  background: #174ea6;
  color: #fff;
  border: none;
  border-radius: 6px;
  padding: 0.5rem 1.25rem;
  font-weight: 500;
  font-size: 1rem;
  cursor: pointer;
  transition: background 0.2s;
}
.btn-primary:hover {
  background: #1251a6;
}
.card-body {
  padding: 1rem 1.5rem 1.5rem 1.5rem;
}
.grid-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 0.5rem;
}
.grid-table th, .grid-table td {
  padding: 1rem 1.5rem;
  text-align: left;
  border-bottom: 1px solid #f0f0f0;
  font-size: 1rem;
}
.grid-table th {
  background: #f9fafb;
  font-weight: 600;
  color: #444;
}
.action-buttons {
  display: flex;
  gap: 0.5rem;
  justify-content: flex-end;
}
.edit-btn, .delete-btn {
  background: none;
  border: none;
  color: #174ea6;
  font-size: 1.15rem;
  margin-right: 0.5rem;
  cursor: pointer;
  transition: color 0.2s;
}
.edit-btn:last-child, .delete-btn:last-child {
  margin-right: 0;
  color: #d32f2f;
}
.edit-btn:hover {
  color: #1251a6;
}
.delete-btn:hover {
  color: #b71c1c;
}
.status-badge {
  display: inline-block;
  padding: 0.35em 0.8em;
  border-radius: 12px;
  font-size: 0.95em;
  font-weight: 500;
  background: #eafbe7;
  color: #388e3c;
}
.status-badge.inactive {
  background: #f0f0f0;
  color: #888;
}
.status-badge.active {
  background: #eafbe7;
  color: #388e3c;
}
.empty-message {
  text-align: center;
  color: #888;
  font-size: 1.05rem;
  padding: 2rem 0;
}
.grid-pagination {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 1.5rem;
  border-top: 1px solid #f0f0f0;
  background: #fff;
  border-radius: 0 0 12px 12px;
}
.pagination-info {
  color: #555;
  font-size: 1rem;
}
.pagination-controls {
  display: flex;
  align-items: center;
  gap: 1rem;
}
.pagination-controls button {
  background: #f9fafb;
  border: none;
  border-radius: 6px;
  padding: 0.4rem 0.7rem;
  font-size: 1rem;
  color: #174ea6;
  cursor: pointer;
  transition: background 0.2s;
}
.pagination-controls button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}
.pagination-controls select {
  border: 1px solid #e0e0e0;
  border-radius: 6px;
  padding: 0.3rem 0.7rem;
  font-size: 1rem;
}
</style>
