<template>
  <div>
    <div class="card">
      <div class="card-header">
        <h3 class="card-title">
          <i class="fas fa-user-tag"></i> Papis Cadastrados
        </h3>
        <button type="button" class="btn btn-primary" @click="showFormHandler()">
          <i class="fas fa-plus"></i> Novo Papel
        </button>
      </div>
      <div class="card-body">
        <TabbedDataTable
          :tabs="[{ id: 'todos', label: 'Todos os Papis' }]"
          v-model="selectedTab"
          :columns="columns"
          :data="papeis"
          :showAddButton="true"
          @edit="showFormHandler"
          @delete="deletePapel"
        />
      </div>
    </div>

    <FormModal
      v-if="showForm"
      :title="isEditing ? 'Editar Papel' : 'Novo Papel'"
      @close="hideFormHandler"
      @save="handleSubmit"
    >
      <form @submit.prevent="handleSubmit">
        <div class="form-group">
          <label class="form-label">Nome do Papel</label>
          <input v-model="formData.nome" type="text" class="form-control" required>
        </div>
        <div class="form-group">
          <label class="form-label">Descrio</label>
          <input v-model="formData.descricao" type="text" class="form-control">
        </div>
        <div class="form-group">
          <label class="form-label">Status</label>
          <select v-model="formData.ativo" class="form-control form-select">
            <option :value="true">Ativo</option>
            <option :value="false">Inativo</option>
          </select>
        </div>
      </form>
    </FormModal>
  </div>
</template>

<script setup>
import { ref } from 'vue'
import FormModal from '@/components/modals/FormModal.vue'
import TabbedDataTable from '@/components/ui/DataTable/TabbedDataTable.vue'

const selectedTab = ref('todos')

const columns = [
  { field: 'nome', label: 'Nome' },
  { field: 'descricao', label: 'Descrio' },
  { field: 'ativo', label: 'Status', type: 'status' },
  { type: 'actions', label: 'Aes' }
]

const papeis = ref([
  {
    id: 1,
    nome: 'Advogado(a) Gestor(a)',
    descricao: 'Supervisiona uma equipe...',
    ativo: true,
    permissoesIds: [2, 4, 6, 8]
  },
  {
    id: 2,
    nome: 'Advogado(a) Parceiro(a)',
    descricao: 'Atua em casos especficos.',
    ativo: true,
    permissoesIds: [2, 4]
  }
])

const showForm = ref(false)
const isEditing = ref(false)
const formData = ref({
  id: null,
  nome: '',
  descricao: '',
  ativo: true
})

const showFormHandler = (papel = null) => {
  showForm.value = true
  if (papel) {
    isEditing.value = true
    formData.value = { ...papel }
  } else {
    isEditing.value = false
    formData.value = {
      id: null,
      nome: '',
      descricao: '',
      ativo: true
    }
  }
}

const hideFormHandler = () => {
  showForm.value = false
  isEditing.value = false
  formData.value = {
    id: null,
    nome: '',
    descricao: '',
    ativo: true
  }
}

const handleSubmit = () => {
  if (isEditing.value) {
    const index = papeis.value.findIndex(p => p.id === formData.value.id)
    if (index !== -1) {
      papeis.value[index] = { ...formData.value }
    }
  } else {
    const newPapel = {
      ...formData.value,
      id: Date.now(),
      permissoesIds: []
    }
    papeis.value.push(newPapel)
  }
  hideFormHandler()
}

const deletePapel = (id) => {
  if (confirm('Tem certeza que deseja excluir este papel?')) {
    papeis.value = papeis.value.filter(p => p.id !== id)
  }
}
</script>
