<template>
  <table class="grid-table">
    <thead>
      <tr>
        <th v-for="column in columns" :key="column.field || column.type">
          {{ column.label }}
        </th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="item in data" :key="item.id">
        <td v-for="column in columns" :key="column.field || column.type">
          <!-- Status -->
          <template v-if="column.type === 'status'">
            <span :class="['status-badge', item[column.field] ? 'active' : 'inactive']">
              {{ column.format ? column.format(item[column.field]) : (item[column.field] ? 'Ativo' : 'Inativo') }}
            </span>
          </template>

          <!-- Aes -->
          <template v-else-if="column.type === 'actions'">
            <div class="action-buttons">
              <button class="edit-btn" title="Editar" @click="$emit('edit', item)">
                <i class="fas fa-edit"></i>
              </button>
              <button class="delete-btn" title="Excluir" @click="$emit('delete', item)">
                <i class="fas fa-trash"></i>
              </button>
            </div>
          </template>

          <!-- Campos padro -->
          <template v-else>
            {{ column.format ? column.format(item[column.field]) : item[column.field] }}
          </template>
        </td>
      </tr>
      <tr v-if="!data?.length">
        <td :colspan="columns.length" class="empty-message">
          {{ emptyMessage }}
        </td>
      </tr>
    </tbody>
  </table>
</template>

<script setup>
defineProps({
  columns: {
    type: Array,
    required: true
  },
  data: {
    type: Array,
    default: () => []
  },
  emptyMessage: {
    type: String,
    default: 'Nenhum registro encontrado'
  }
})

defineEmits(['edit', 'delete'])
</script>

<style scoped>
.grid-table {
  width: 100%;
  border-collapse: collapse;
}

.grid-table th,
.grid-table td {
  padding: 1rem;
  text-align: left;
  border-bottom: 1px solid var(--border-color, #eee);
}

.grid-table th {
  font-weight: 500;
  color: var(--text-primary);
  background: var(--gray-50, #f9fafb);
}

.grid-table td {
  color: var(--text-secondary);
}

.grid-table tbody tr:hover {
  background-color: var(--gray-50, #f9fafb);
}

.status-badge {
  display: inline-flex;
  align-items: center;
  padding: 0.375rem 0.75rem;
  border-radius: 999px;
  font-size: 0.875rem;
  font-weight: 500;
}

.status-badge.active {
  background: var(--success-50, #ecfdf5);
  color: var(--success, #059669);
}

.status-badge.inactive {
  background: var(--gray-100, #f3f4f6);
  color: var(--gray-600, #4b5563);
}

.action-buttons {
  display: flex;
  gap: 0.5rem;
  justify-content: flex-end;
}


.edit-btn,
.delete-btn {
  background: none;
  border: none;
  color: #174ea6;
  font-size: 1.15rem;
  margin-right: 0.5rem;
  cursor: pointer;
  transition: color 0.2s;
}
.edit-btn:last-child,
.delete-btn:last-child {
  margin-right: 0;
  color: #d32f2f;
}
.edit-btn:hover {
  color: #1251a6;
}
.delete-btn:hover {
  color: #b71c1c;
}

.empty-message {
  text-align: center;
  color: var(--text-secondary);
  padding: 2rem !important;
  font-style: italic;
}
</style>