<template>
  <div>
    <div class="card">
      <div class="card-header">
        <h3 class="card-title">
          <i class="fas fa-layer-group"></i> Agrupadores
        </h3>
        <button type="button" class="btn btn-primary" @click="showFormHandler()">
          <i class="fas fa-plus"></i> Novo Agrupador
        </button>
      </div>
      <div class="card-body">
        <div class="tabs">
          <button 
            class="tab-button" 
            :class="{ active: activeTab === 'FUNCIONALIDADE' }"
            @click="activeTab = 'FUNCIONALIDADE'"
          >
            Funcionalidades
          </button>
          <button 
            class="tab-button" 
            :class="{ active: activeTab === 'PERMISSAO' }"
            @click="activeTab = 'PERMISSAO'"
          >
            Permisses
          </button>
        </div>
        
        <table class="grid-table">
          <thead>
            <tr>
              <th>Nome</th>
              <th>Descrio</th>
              <th>Status</th>
              <th class="action-buttons">Aes</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="agrupador in filteredAgrupadores" :key="agrupador.id">
              <td>{{ agrupador.nome }}</td>
              <td>{{ agrupador.descricao }}</td>
              <td>
                <span :class="['status-badge', agrupador.ativo ? 'active' : 'inactive']">
                  {{ agrupador.ativo ? 'Ativo' : 'Inativo' }}
                </span>
              </td>
              <td class="action-buttons">
                <button class="edit-btn" title="Editar" @click="showFormHandler(agrupador)">
                  <i class="fas fa-edit"></i>
                </button>
                <button class="delete-btn" title="Excluir" @click="deleteAgrupador(agrupador.id)">
                  <i class="fas fa-trash"></i>
                </button>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>

    <FormModal
      v-if="showForm"
      :title="isEditing ? 'Editar Agrupador' : 'Novo Agrupador'"
      @close="hideFormHandler"
      @save="handleSubmit"
    >
      <form @submit.prevent="handleSubmit">
        <div class="form-group">
          <label class="form-label">Nome do Agrupador</label>
          <input v-model="formData.nome" type="text" class="form-control" required>
        </div>
        <div class="form-group">
          <label class="form-label">Descrio</label>
          <input v-model="formData.descricao" type="text" class="form-control">
        </div>
        <div class="form-group">
          <label class="form-label">Status</label>
          <select v-model="formData.ativo" class="form-control form-select">
            <option :value="true">Ativo</option>
            <option :value="false">Inativo</option>
          </select>
        </div>
        <div class="form-group">
          <label class="form-label">Tipo</label>
          <select v-model="formData.tipo" class="form-control form-select" required>
            <option value="FUNCIONALIDADE">Funcionalidade</option>
            <option value="PERMISSAO">Permisso</option>
          </select>
        </div>
      </form>
    </FormModal>
  </div>
</template>

<script setup>
import { ref, computed } from 'vue'
import FormModal from '@/components/modals/FormModal.vue'

const agrupadores = ref([
  {
    id: 1,
    nome: 'Gesto de Processos',
    descricao: 'Funcionalidades relacionadas  gesto processual',
    tipo: 'FUNCIONALIDADE',
    ativo: true
  },
  {
    id: 2,
    nome: 'Relatrios',
    descricao: 'Funcionalidades de relatrios e dashboards',
    tipo: 'FUNCIONALIDADE',
    ativo: true
  },
  {
    id: 3,
    nome: 'Acesso Bsico',
    descricao: 'Permisses bsicas do sistema',
    tipo: 'PERMISSAO',
    ativo: true
  }
])

const showForm = ref(false)
const isEditing = ref(false)
const activeTab = ref('FUNCIONALIDADE')

const formData = ref({
  id: null,
  nome: '',
  descricao: '',
  tipo: 'FUNCIONALIDADE',
  ativo: true
})

const filteredAgrupadores = computed(() => {
  return agrupadores.value.filter(a => a.tipo === activeTab.value)
})

const showFormHandler = (agrupador = null) => {
  showForm.value = true
  if (agrupador) {
    isEditing.value = true
    formData.value = { ...agrupador }
  } else {
    isEditing.value = false
    formData.value = {
      id: null,
      nome: '',
      descricao: '',
      tipo: activeTab.value,
      ativo: true
    }
  }
}

const hideFormHandler = () => {
  showForm.value = false
  isEditing.value = false
  formData.value = {
    id: null,
    nome: '',
    descricao: '',
    tipo: activeTab.value,
    ativo: true
  }
}

const handleSubmit = () => {
  if (isEditing.value) {
    const index = agrupadores.value.findIndex(a => a.id === formData.value.id)
    if (index !== -1) {
      agrupadores.value[index] = { ...formData.value }
    }
  } else {
    const newAgrupador = {
      ...formData.value,
      id: Date.now()
    }
    agrupadores.value.push(newAgrupador)
  }
  hideFormHandler()
}

const deleteAgrupador = (id) => {
  if (confirm('Tem certeza que deseja excluir este agrupador?')) {
    agrupadores.value = agrupadores.value.filter(a => a.id !== id)
  }
}
</script>

<style scoped>
.tabs {
  margin-bottom: 1rem;
  border-bottom: 1px solid var(--gray-200);
}

.tab-button {
  padding: 0.5rem 1rem;
  margin-right: 0.5rem;
  border: none;
  background: none;
  cursor: pointer;
  opacity: 0.7;
  border-bottom: 2px solid transparent;
}

.tab-button.active {
  opacity: 1;
  border-bottom-color: var(--primary);
  font-weight: 500;
}

.tab-button:hover {
  opacity: 1;
}
</style>